.cartContainer{
    height: 94vh;
    width: 100vw;
}
.cartTitle{
    height: 20%;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0px 50px;
    border-bottom: 1px solid rgb(46, 46, 46);
}
.cartCards{
    width: 100%;
    min-height: 80%;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}